export default {
	data() {
		return {
			list: [],
			tabCur: 0,
			mainCur: 0,
			verticalNavTop: 0,
			load: true,
			length: 0,
			keys: {}
		};
	},
	onLoad() {
		this.getData();
	},
	onReady() {

	},
	methods: {
		toTel(number) {
			window.location.href = "tel:" + number;
		},
		getData() {
			uni.post("/api/security/GetAddressBook", {}, msg => {
				let length = 0;
				for (let i in msg.data) {
					this.keys[length] = i;
					msg.data[i].id = length++;
				}
				this.list = msg.data;
				this.length = length;
			})
		},
		TabSelect(e) {
			this.tabCur = e.currentTarget.dataset.id;
			this.mainCur = e.currentTarget.dataset.id;
			this.verticalNavTop = (e.currentTarget.dataset.id - 1) * 50
		},
		VerticalMain(e) {



			let that = this;
			let tabHeight = 0;
			if (this.load) {
				for (let i = 0; i < this.length; i++) {
					let key = this.keys[i];
					let view = uni.createSelectorQuery().select("#main-" + i);
					view.fields({
						size: true
					}, data => {
						that.list[key].top = tabHeight;
						tabHeight = tabHeight + data.height;
						that.list[key].bottom = tabHeight;
					}).exec();
				}
				this.load = false
			}
			let scrollTop = e.detail.scrollTop + 10;
			for (let i = 0; i < this.length; i++) {
				let key = this.keys[i];
				if (scrollTop > this.list[key].top && scrollTop < this.list[key].bottom) {
					this.verticalNavTop = (i - 1) * 50;
					this.tabCur = i;
					return false;
				}
			}
		}
	},
}
